@import url(https://fonts.googleapis.com/css?family=Oleo+Script);
@import url(https://fonts.googleapis.com/css?family=Lato|Oleo+Script);
body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
  align-items: center;
  display: flex;
  padding: 0px;
  flex-direction: column;
  width: 100vw;
  overflow: hidden;
}

.title { 
  font-size: 90px;
}

.whiten {
  -webkit-filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(24deg) brightness(10002%) contrast(10001%);
          filter: invert(100%) sepia(0%) saturate(0%) hue-rotate(24deg) brightness(10002%) contrast(10001%);
}

h1 {
  font-family: 'Oleo Script', cursive;
  font-size: 50px;
  line-height: 1em;
  margin-bottom: 0px;
  font-weight: bold;
}

h2 {
  font-family: 'Oleo Script', cursive;
  font-size: 30px;
  margin-bottom: 0em;
  font-weight: bold;
  line-height: 1em;
}

h3 {
  font-family: 'Lato', sans-serif;
  font-size: 2em;
  margin-bottom: 0.2em;
  font-weight: bold;
}

h4 {
  font-family: 'Lato', sans-serif;
  font-size: 1.4em;
  margin-bottom: 0.2em;
  font-weight: bold;
}

p {
  margin-top: 0px;
  font-family: 'Lato', sans-serif;
  line-height: 1.1em;
  font-size: 1.3em;
}

.tightText {
  line-height: 0.3em;
}

.textBox {
  max-width: 600px;
  align-self: center;
}

.nobold {
  font-weight: normal;
}

.bold {
  font-weight: bold;
}

.mockups {
  max-width: 80%;
  max-height: 50vh;
  object-fit: contain;
  flex: 1 1;
  margin-top: -15vh;
  z-index: 2;
}

.mockups_container {
  background-image: url('/images/macbook grayscale bg.png');
  background-repeat: no-repeat;
  background-size: contain;
  background-position: center left;
  flex-direction: column;
  height: 40vh;
  margin-top: 5vw;
  opacity: 1;
  z-index: 0;
  display: flex;
}

footer {
  background: -webkit-linear-gradient(25deg, #000 0, #FF00E5 150%);
  background: linear-gradient(65deg, #000 0, #FF00E5 150%);
  width: 100%;
  padding: 40px;
}

.header_bg_container {
  width: 100%;
  background: 
    url('/images/4_closets.png') 0px 0px;
  background-size:
    cover;
  background-repeat: 
    no-repeat;
  display: flex;
  align-items: stretch;
  flex-direction: column;
  min-height: 60vh;
}

.header_bg_darken {
  background: rgba(0,0,0,0.6);
  flex: 1 1;
  justify-content: space-between;
  display: flex;
  align-items: stretch;
  flex-direction: column;
}

.header_bg_container2: {
  background-color: red;
}

.header_content {
  flex: 1 1;
  justify-content: center;
  display: flex;
  align-items: stretch;
  flex-direction: column;
  padding: 20px;
}

header: {
  background-color: red;
  padding: 40px;
}

.step_cols {
  padding: 20px;
  height: 100%;
}

.card_img {
  height: 200px;
  object-fit: cover;
  overflow:hidden;
  padding-bottom:0%;
}

.card_textbox {
  height: 68px;
  padding: 10px;

  display: flex;
  justify-content: center;
  line-height: 1.5em;
  align-items: center;
}

.feature_tile {
  margin-top: 20px;
}

.square {
    float:left;
    position: relative;
    width: 100%;
    padding-bottom : 100%; /* = width for a 1:1 aspect ratio */
    background-position:center center;
    background-repeat:no-repeat;
    background-size:cover; /* you change this to "contain" if you don't want the images to be cropped */
}

.purple {
  color: #9B51E0;
}

.white_text {
  color: #FFF;
}

.purple_pink_bg {
  font-weight: bold;
  background: -webkit-linear-gradient(25deg, #9B51E0 0, #FF00E5 100%);
  background: linear-gradient(65deg, #9B51E0 0, #FF00E5 100%);
}

#primary-gradient {
  background: -webkit-linear-gradient(25deg, #9B51E0 0, #FF00E5 100%);
  background: linear-gradient(65deg, #9B51E0 0, #FF00E5 100%);
  border-width: 0;
}

.center_row {
  align-items: center;
  display: flex;
  flex-direction: column;
  width: 100%;
}

.card_cols {
  margin-top: 20px;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
  pointer-events: none;
}

.avatar {
  margin-bottom: 30px;
  height: 140px;
  width: 140px;
  border-radius: 70px;
  object-fit: cover
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.early_access_form {
  flex-direction: column;
  display: flex;
  width: 100%;
  align-items: stretch;
  justify-content: center;
  max-width: 600px;
}

.body {
  display: flex;
  flex-direction: column;
  align-items: stretch;
  padding: 20px;
  max-width: 1000px;
}

.ant-btn-primary {
  background-image: -webkit-linear-gradient(25deg, #9B51E0 0, #FF00E5 100%);
  background-image: linear-gradient(65deg, #9B51E0 0, #FF00E5 100%);
}

.ant-tabs-bottom {
  border-top: 0px solid #e8e8e8;
}

.ant-tabs-ink-bar {
  background-color: #fff;
}

.carousel_container {
  background-color: #000;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: space-around;
}

.carousel_row {
  width: 100%;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-around;
}

.logo_row {
  background-color: 'blue';
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: row;
  padding-bottom: 30px;
  padding-top: 10px;
}

.logo_img {
  height: 20px;
  margin-left: 5vw;
}

.ant-carousel .slick-slide {
  text-align: center;
  width: 100%;
  background: #000;
  overflow: hidden;
}
